//
export let inpatient_information = {
  id: 101,
  slug: "inpatient_information",
  version: 1,
  multiple: false,
  fields: [
    {
      name: "admissionDate",
      type: "date",
      label: "",
      required: true,
      isLockedField: true,
      validator: {
        formula: "$dischargeDate#>$admissionDate#",
        fields: ["dischargeDate"],
        message: ""
      }
    },
    {
      name: "admissionAge",
      label: "",
      type: "number",
      editable: false,
      isLockedField: true,
      placeholder: "",
      required: true,
      rely: {
        formula: "GETAGERANGE($birthday#,$admissionDate#)",
        fields: [
          { name: "birthday", position: "client-extra" },
          "admissionDate"
        ]
      }
    },
    {
      name: "dischargeDate",
      type: "date",
      label: "",
      isLockedField: true,
      validator: {
        formula: "$dischargeDate#>$admissionDate#",
        fields: ["admissionDate"],
        message: ""
      },
      required: true
    },
    {
      name: "field_16",
      label: "ICU",
      unit: "d",
      allowDecimals: false,
      allowNegative: false,
      required: true,
      type: "number"
    },
    {
      name: "field_17",
      label: "",
      unit: "h",
      allowNegative: false,
      required: true,
      type: "number"
    },
    {
      name: "field_19",
      label: "EQ-5D",
      type: "number"
    },
    {
      name: "field_20",
      label: "EQ-5D",
      type: "number"
    },
    {
      name: "field_14",
      label: "",
      required: false,
      placeholder: "",
      editable: false,
      type: "number",
      rely: {
        formula: "COUNTDAYRANGE($admissionDate#,$dischargeDate#)",
        fields: ["admissionDate", "dischargeDate"]
      }
    },
    {
      name: "field_18",
      label: "()",
      required: true,
      type: "number",
      allowNegative: false
    },
    {
      name: "field_15",
      label: "",
      placeholder: "",
      editable: false,
      required: false,
      type: "number",
      rely: {
        formula: "COUNTDAYRANGE($firstSurgeryDate#,$dischargeDate#)",
        fields: [
          { name: "firstSurgeryDate", position: "client-extra" },
          "dischargeDate"
        ]
      }
    }
  ]
};



