export default {
  create() {
    return {
      title: "",
      version: 1,
      multiple: false,
      fields: [
        {
          name: "label",
          label: "",
          labelStyle: "block",
          type: "text"
        },
        {
          name: "description",
          label: "",
          labelStyle: "block",
          type: "textarea"
        },
        {
          name: "options",
          label: "",
          labelStyle: "block",
          type: "subform",
          multiple: true,
          display: {
            layout: "base-form"
          },
          fields: [
            {
              name: "field_" + Date.now(),
              enAbleLabel: false,
              label: "",
              type: "text",
              placeholder: ""
            },
            {
              name: "field_" + Date.now() + 1,
              enAbleLabel: false,
              label: "",
              type: "text",
              placeholder: ""
            }
          ]
        }
      ]
    };
  }
};



