export default {
  title: "",
  version: 1,
  multiple: false,
  fields: [
    {
      name: "name",
      label: "",
      type: "text"
    },
    {
      name: "version",
      label: "",
      type: "number"
    },
    {
      name: "layout",
      label: "",
      type: "radio",
      options: [
        {
          label: "",
          value: "base-form"
        },
        {
          label: "",
          value: "table-form"
        }
      ]
    },
    {
      name: "multiple",
      label: "",
      type: "radio",
      options: [
        {
          label: "",
          value: true
        },
        {
          label: "",
          value: false
        }
      ]
    }
  ]
};



