export default {
  create() {
    return {
      title: "",
      version: 1,
      multiple: false,
      fields: [
        {
          name: "label",
          label: "",
          type: "text"
        },
        {
          name: "description",
          label: "",
          type: "textarea"
        },
        {
          name: "allowDecimals",
          label: "",
          type: "radio",
          options: [
            {
              label: "",
              value: true
            },
            {
              label: "",
              value: false
            }
          ]
        },
        {
          name: "allowNegative",
          label: "",
          type: "radio",
          options: [
            {
              label: "",
              value: true
            },
            {
              label: "",
              value: false
            }
          ]
        }
      ]
    };
  }
};



