import fecha from "fecha";

fecha.masks = {
	default: "YYYY-MM-DD HH:mm:ss",
	shortDatetime: "YYYY-MM-DD HH:mm",
	date: "YYYY-MM-DD",
	time: "HH:mm:ss",
	month: "YYYY-MM"
};

/**
 * Dateunix timestamp
 * 
 * import times from '../../../Resources/utils/wt-time-utils'
 * times.format(1494554766)	//unix timestamp
 * times.format(1494554766, 'shortDatetime')	// unix timestamp
 * times.format(new Date())	// Date
 */

export default {
	/**
	 * Dateunix timestamp
	 */
	dateToTimestamp: function(date) {
		return Math.round(date.getTime());
	},
	/**
	 * unix timestampDate
	 * @param {int} timestamp
	 */
	timestampToDate: function(timestamp) {
		return new Date(timestamp);
	},
	/**
	 * unix timestamp
	 */
	getCurrentTimestamp: function() {
		return this.dateToTimestamp(new Date());
	},
	/**
	 * Convertdatedatetimestampstringdate
	 */

	toDate: function(dateObject) {
		let date = null;
		if (dateObject instanceof Date) {
			date = dateObject;
		} else if (typeof dateObject == "string") {
			date = new Date(dateObject);
		} else if (typeof dateObject == "number") {
			if (dateObject == 0) {
				date = null;
			} else {
				date = this.timestampToDate(dateObject);
			}
		}
		return date;
	},
	/**
	 * unix timestampDate
	 * :
	 * 	'default' - 'YYYY-MM-DD HH:mm:ss'
	 * 	'shortDatetime' - 'YYYY-MM-DD HH:mm'
	 * 	'date' - 'YYYY-MM-DD'
	 * 	'time' - 'HH:mm:ss'
	 */
	format: function(dateObject, format = "default") {
		let date = this.toDate(dateObject);
		if (date == null) {
			return "";
		}
		return fecha.format(date, format);
	}
};



