<template>
    <el-date-picker
    :value="value"
	ref="ssss"
    type="date"
    v-bind="$attrs"
	@input="updateValue"
    :max-width="200"
    placeholder="">
      </el-date-picker>
</template>
<script>
export default {
	name: "v-Date",
	props: {
		value: [Date, Number, String],
		options: {
			type: Object,
			default: {}
		}
	},
	data() {
		return {};
	},
	methods: {
		updateValue(val) {
			val = Date.parse(val);
			this.$emit("input", val);
			this.$nextTick(_ => this.$emit("blur")); //blurblur,
		}
	},
	style: {
		"min-width": 150
	}
};
</script>
<style lang="less" scoped>
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
	width: auto;
}
</style>




